@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;300&display=swap);
*{
  padding: 0;
  margin: 0;
  font-family: 'Poppins', sans-serif;
}

body{
  background-color: #f3f4f6;
}

/*----- Container -------*/

.container{
  width: 100%;
  height: 90vh;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.turn{
  width: 300px;
  text-align: center;
  margin: 30px 0px 10px 0px;
  color: #e45c51;
}

.sub_container{
  display: -webkit-flex;
  display: flex;
  max-width: 700px;
  width: 80%;
  height: 400px;
}

/*--------- Game --------*/
.game{
  width: 70%;
  height: 100%;
  background-color: white;
  border-radius: 7px;
  box-shadow: 1px 1px 1px 1px #eaeaea;
  padding: 20px;
}
.row{
  width:100%;
  height: 33%;
  border-bottom: 1px solid #d5d5d5;
  display: -webkit-flex;
  display: flex;
}
.row:nth-child(3){
  border: none;
}
@media screen and (max-width: 600px) {
    .sub_container {
        width: 90%;
        -webkit-justify-content: center;
                justify-content: center;
    }
    .game{
        width: 100%;
    }
}

.header{
    width: 100%;
    padding: 10px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    box-sizing: border-box;
    background-color: white;
    box-shadow: 1px 1px 1px 1px #eaeaea;
}
.div_logo{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}
.div_logo h1{
    font-size: 20px;
    margin-left: 10px;
    color: #e45c51;
}
.logo{
    width: 30px;
    margin-left: 10px;
}
.nav_ul{
    display: -webkit-flex;
    display: flex;
    list-style: none;
    margin-right: 10px;
}
.nav_ul li {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    margin-left: 30px;
    cursor: pointer;
}
.nav_ul li img {
    width: 20px;
    margin-right: 10px;
}
@media screen and (max-width: 600px) {
    .item{
        display: none;
    }
}
.Square{
    display: -webkit-flex;
    display: flex;
    height: 100%;
    -webkit-flex: 1 1;
            flex: 1 1;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    border: 2px solid #d5d5d5;
    cursor: pointer;
}

.Square span{
    font-size: 50px;
    font-weight: bold;
}
::-webkit-scrollbar {
    width: 10px;
}

::-webkit-scrollbar-track {
    background: white; 
}

::-webkit-scrollbar-thumb {
    background: #e45c51; 
}

::-webkit-scrollbar-thumb:hover {
    background: #555; 
}
.register{
    width: 30%;
    height: 100%;
    margin-right: 20px;
    background-color: white;
    box-shadow: 1px ;
    box-shadow: 1px 1px 1px 1px #eaeaea;
    border-radius: 7px;
}
.register_header{
    width: 100%;
    text-align: center;
    font-size: 12px;
    padding: 5px;
    box-sizing: border-box;
    border-bottom: 1.6px solid #d5d5d5;
}
  
.register_main{
    width: 100%;
    height: 85%;
    margin-top:10px;
    overflow-y: scroll;
}
.register_data{
    width: 100%;
    text-align: center;
    margin-bottom: 10px;
}

@media screen and (max-width: 600px) {
    .register {
        display: none;
    }
}
.popup{
    width: 100%;
    height: 100vh;
    position: absolute;
    top: 0px;
    background-color: rgb(0 0 0 / 63%);
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.message{
    width: 500px;
    height: 150px;
    background-color: white;
    border-radius: 10px;
    -webkit-animation: anim 0.3s forwards;
            animation: anim 0.3s forwards;
}
.message_header{
    width: 100%;
    padding: 5px;
    box-sizing: border-box;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    -webkit-align-items: center;
            align-items: center;
    border-bottom: 1px solid #d5d5d5;
}
.message_header span{
    font-weight: bold;
    font-size: 20px;
    color: #e45c51;
    margin-right: 10px;
    cursor: pointer;
}

.message_body{
    text-align: center;
    margin-top: 30px;
}

@-webkit-keyframes anim {
    from{
        opacity: 0;
        -webkit-transform: translateY(50px);
                transform: translateY(50px);
    }
    to{
        opacity: 1;
        -webkit-transform: translateY(0px);
                transform: translateY(0px);
    }
}

@keyframes anim {
    from{
        opacity: 0;
        -webkit-transform: translateY(50px);
                transform: translateY(50px);
    }
    to{
        opacity: 1;
        -webkit-transform: translateY(0px);
                transform: translateY(0px);
    }
}
